.tweet {
  margin: auto;
  margin-top: 40vh;
  border: 0px;
  border-radius: 25px;
  background-color: rgb(199, 224, 255);
  width: 564px;
  min-height: 70px;
  padding: 10px;
  display: flex;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 18px;
  
}

.avatar {
  width: 48px;
  height: 48px;
  border-radius: 25px;
  margin-right: 10px;
}

.name {
  font-weight: bold;
  margin-bottom: 0.5em;
  margin-right: 0.3em;
}

.handle {
  color: #4b555d;
  font-size: 13px;
}

.time {
  padding-left: 0.3em;
  color: #4b555d;
}

.time::before {
  content: "\00b7";
  padding-right: 0.3em;
}

.button {
  margin-top: 10px;
  margin-left: 2px;
  font-size: 1.4em;
  color: #797979;
}

.button i {
  width: 80px;
}